import React,{ useState, useEffect }  from 'react';
import axios from "axios";



export default function Init() {
    const [ settings, setSettings ] = useState({})
    //fetch("mydata.json")
    //    .then(res => res.json())
    //    .then(setStyle)
    useEffect(() => {
        const fetchData = async () => {
            const result = await axios(
                'mydata.json',
            );
            setSettings(result.data);
        };
        fetchData();
    }, []);

    return(
        settings
    )

}

