import React,{ useState, useEffect }  from 'react';
import { Helmet } from 'react-helmet'
import logo from './logo.svg';
import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,Redirect,
  useParams,
  useRouteMatch
} from "react-router-dom";
import Init from './init';
import Chat from './chat';
import Chatlist from './chatlist';
import Topbanner from './topbanner';


import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import RestoreIcon from '@material-ui/icons/Restore';
import FavoriteIcon from '@material-ui/icons/Favorite';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import axios from 'axios';


const indexTitle="珍爱交友网"
const siteId="jiaoyou0222"



var styles={
    bodydiv:{
        fontSize:"16px",
        height:"100%",
        background:"grey",
        backgroundImage: 'url("/bkimage.jpg")',
        backgroundPosition: "center",
        backgroundRepeat: "no-repeat",
        backgroundSize: "cover",
        position: "relative"
    },
    framediv:{
        display:"flex",
        flexDirection:"column",
        margin:"auto",
        maxWidth:"780px",
        height:"100%",
        background:"grey",
        minHeight:"300px",
        maxHeight:"820px"
    },
    topdiv:{
        flex:"none",
        height:"30px",
        background:"orange"
    },
    maindiv:{
        flex:"auto",
        overflowY:"auto",
        overflowX:"hidden"
    },
    bottomdiv:{
        flex:"none",
        background:"green"
    },
}



function SiteRouter(){


    return(

        <Router>
            <Switch>


                <Route exact path="/">
                    <Dashboard/>
                </Route>
                <Route path="/:id">
                    <Dashboard/>
                </Route>

            </Switch>
        </Router>
    );
}





function Dashboard() {
    let { path, url } = useRouteMatch();

    const setting=Init()

    const routes = [
        {
            path: "/",
            exact: true,
            main: () => <h2>Home</h2>
        },
        {
            path: "/chattest",
            main: () => Chatlist()
        },
        {
            path: "/chat",
            main: ()=>Chat()
        },
        {
            path: "/profile",
            main: ()=><h2>Profile</h2>
        }
    ];

    const [value, setValue] = React.useState(0);



    return (
        <React.Fragment>
            <Helmet>
                <title>{indexTitle}</title>
            </Helmet>
            <Router>
                <div style={styles.bodydiv}>
                    <div style={styles.framediv}>

                        <div style={styles.topdiv}>
                            {Topbanner()}
                        </div>

                        <div style={styles.maindiv}>
                            <Switch>

                                {routes.map((route, index) => (
                                    // Render more <Route>s with the same paths as
                                    // above, but different components this time.
                                    <Route
                                        key={index}
                                        path={path+route.path}
                                        exact={route.exact}
                                        children={<route.main />}
                                    />
                                ))}
                            </Switch>
                        </div>



                        <div style={styles.bottomdiv}>
                            <BottomNavigation
                                value={value}
                                onChange={(event, newValue) => {
                                    setValue(newValue);
                                }}
                                showLabels
                            >
                                <BottomNavigationAction label="匹配" icon={<RestoreIcon />} component={Link} to={url+"/"}/>
                                <BottomNavigationAction label="聊天测试" icon={<FavoriteIcon />} component={Link} to={url+"/chattest"}/>
                                <BottomNavigationAction label="聊天" icon={<FavoriteIcon />} component={Link} to={url+"/chat"}/>
                                <BottomNavigationAction label="个人信息" icon={<LocationOnIcon />} component={Link} to={url+"/profile"}/>
                            </BottomNavigation>
                        </div>

                    </div>

                </div>
            </Router>
        </React.Fragment>
    );
}




function App() {

  return (

          <SiteRouter/>
  );
}

export default App;
