import React,{ useState, useEffect }  from 'react';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link,
    useParams,
    useRouteMatch
} from "react-router-dom";
import {chats, messages} from './db';
import Avatar from '@material-ui/core/Avatar';
import axios from "axios";

var styles={
    chatframediv:{
        display:"flex",
        flexDirection:"row",
        width:"100%",
        height:"100%"
    },
    chatlistparentdiv:{
        flex:"none",
        width:"70px",
        height:"100%",
        background:"darkgrey",
        overflowX:"hidden",
        overflowY:"hidden",
    },

    chatlistdiv:{
        flex:"none",
        width:"87px",
        height:"100%",
        background:"darkgrey",
        overflowX:"hidden",
        overflowY:"scroll",
        paddingRight:"17px"

    },

    chatlistinnerdiv:{
        fontSize:"10px",
        fontColor:"black",
        textDecoration:"none",
        paddingTop:"5px",
        paddingBottom:"5px",
        background:"darkgrey",
        textAlign :"center"
    },
    chatlistinnerdivactive:{
        fontSize:"10px",
        fontColor:"black",
        textDecoration:"none",
        paddingTop:"15px",
        paddingBottom:"15px",
        background:"grey",
        textAlign :"center"
    },
    chatlistlink:{
      textDecoration: "none",
      color:"black",
    },
    chatlistimgdiv:{
        display:"block",
        margin:"auto",
    },
    chatmessagediv:{
        flex:"auto",
        overflowY:"auto",
        overflowX:"hidden"
    },

}

export default function Chat() {
    let { path, url } = useRouteMatch();
    const[chatlist,updatechatlist]=useState(chats)


    const handleListClick=(id)=>{
        var i,item
        for (i=0; i<chatlist.length; i++){
            item = chatlist[i];
            if (item.id===id){
                chatlist.splice(i,1);
                chatlist.unshift(item);
            }
        }

        updatechatlist(chatlist);

    };


    return (
        <div style={styles.chatframediv}>
            <div style={styles.chatlistparentdiv}>
            <div style={styles.chatlistdiv}>

                {chatlist.map((chat,index) => (
                    <div style={index===0?styles.chatlistinnerdivactive:styles.chatlistinnerdiv}  onClick={()=>handleListClick(chat.id)} >
                        <Link to={`${url}/${chat.id}`} style={styles.chatlistlink}>
                        <Avatar src={chat.picture}  style={styles.chatlistimgdiv}/>
                        {index===0?(''):(<div>{chat.name}</div>)}

                        </Link>
                    </div>
                ))}

            </div>
            </div>

            <div style={styles.chatmessagediv}>
            <Switch>
                <Route exact path={path}>
                    <h3>Please select a topic.</h3>
                </Route>
                <Route path={`${path}/:topicId`}>
                    <Topic />
                </Route>
            </Switch>
            </div>
        </div>
    );

}

function Topic() {

    let { topicId } = useParams();


    return (
        <div>
            <h3>this is chat with {topicId}</h3>
        </div>
    );
}
